import React from "react";
import Item from "./Item";
import { Divider, Input, Box } from "@chakra-ui/react";

class ToDoDisplay extends React.Component {
  render() {
    return (
      <span>
        <Box
          p="30px"
          pb="60px"
          m="10px"
          borderWidth="1px"
          borderRadius="lg"
          overflow="hidden"
        >
          {this.props.todoList.map((item, index) => {
            return (
              <>
                <Item
                  index={index}
                  onDelete={this.props.onDelete}
                  key={index}
                  item={item}
                />
                <Divider /> 
              </>
            );
          })}
        </Box>
      </span>
    );
  }
}

export default ToDoDisplay;
